<template>
  <div class="aboutUs">
    <navigation-bar
      class="abount-nav-bar"
      pageName="关于我们"
      :border="false"
      @onLeftClick="onBack"
    />
    <div class="mineHeader">
      <img class="minUserImg" :src="propertyIcon" />
        <div class="mineHeadTitle">信我家-物业</div>
        <div class="mineHeadVersion">{{version}}</div>
    </div>
    <div class="mineContent">
      <van-cell
        class="mineContentCell"
        :key="item.id"
        v-for="item in pageData"
        :icon="item.leftIcon"
        :title="item.title"
        :value="item.value"
        @click="cellClicked(item.id)"
        is-link
      >
        <div slot="right-icon" class="right-icon-div">
          <div class="tip" v-if="msgTip && item.id == 0"></div>
          <van-icon :name="item.rightIcon" class="right-vant-icon"></van-icon>
        </div>
      </van-cell>
    </div>
  </div>
</template>

<script>
import { store, storage } from "../../utils/store";
import config from "../../common/config";
import NavigationBar from "../title/NavigationBar";

import Vue from "vue";
import { Icon, Cell, CellGroup, Toast, Dialog, ActionSheet } from "vant";
Vue.use(Cell)
  .use(CellGroup)
  .use(Icon)
  .use(Toast)
  .use(Dialog)
  .use(ActionSheet);

import { deleteAlias, cleanTags } from "../../services/loginService";
import log from '../../utils/log'

// 头部背景图片
import topPic from "@/assets/imgs/mine/mine_mine.png";
import propertyIcon from "@/assets/imgs/mine/icon_property.png";
// 箭头icon
import arrowIcon from "@/assets/imgs/mine/mine_arrow.png";
export default {
  name: "AboutUs",
  components: { NavigationBar },
  created() {
    // 加载页面数据
    const platform = navigator.userAgent;
    const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    if (isiOS) {
      this.pageData = [
        {
          id: 0,
          title: "关于",
          leftIcon: '',
          rightIcon: arrowIcon,
          value: ""
        }
      ]
    }
  },
  data() {
    return {
      active: 0,
      // 图片和icon引入
      topPic: topPic,
      propertyIcon: propertyIcon,

      // 页面数据
      pageData: [
        {
          id: 0,
          title: "关于",
          leftIcon: '',
          rightIcon: arrowIcon,
          value: ""
        },
        {
          id: 1,
          title: "检查更新",
          leftIcon: "",
          rightIcon: arrowIcon,
          value: ""
        }
      ],
      msgTip: false,
      version: config.version,
      isAndroid: false,
    };
  },
  watch: {
    isShowenvSwitch(showing) {
      store.get("hideTabBar")(showing);
    }
  },
  methods: {
    onBack() {
      history.back();
    },
    cellClicked: function(idType) {
      switch (idType) {
        case 0:
          // 关于
          this.$router.push({ name: "mineAbout" });
          break;
        case 1:
          // 版本更新
          this.checkNewVersion();
          break;
        default:
          break;
      }
    },
    checkNewVersion() {
      let ota = navigator.ota;
      let obj = {};
      ota.ota(obj,
        (resultStatus) => {
          console.log("Call ota success. ");
        },
        (message) => {
          console.log("Call ota fail. " + message);
          if (message == "没有新版本") {
            this.showLatestDialog(this.version);
          } else {
            Dialog.alert({
              title: message
            }).then(() => {});
          }
        })
    },
    showLatestDialog(version) {
      Dialog.alert({
        title: "已经是最新版本了哦",
        message: "版本号：" + version
      }).then(() => {});
    },
  }
};
</script>

